.productList {
    display: flex;
    /* задали перенос для flex элементов */
    flex-wrap: wrap;
    background-color: dimgray;
    /* убрали стили у <li> */
    list-style-type: none;
    margin: 0;
  }
  
  .productCard {
    margin: 30px;
    box-sizing: border-box;
    padding: 15px;
    display: flex;
    flex-direction: column;
    background-color: #fff;
    /* закругление карточки */
    border-radius: 15px;
    align-items: center;
  }
  
  .imgContainer {
    height: 380px;
    width: 280px;
    overflow: hidden;
  }
  
  .title {
    /* свойства для авто переноса слов */
    word-wrap: break-word;
    width: 200px;
    margin: 10px;
  }
  
  .image {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }